<template><section>
<h1>多选框</h1>
<p>一组备选项中进行多选</p>
<h2 id="ji-chu-yong-fa"><a class="header-anchor" href="#ji-chu-yong-fa" aria-hidden="true">¶</a> 基础用法</h2>
<demo-block :jsfiddle="{&quot;html&quot;:&quot;&lt;xl-checkbox v-model=\&quot;value\&quot; label=\&quot;选择框\&quot;&gt;&lt;/xl-checkbox&gt;\n&lt;p&gt;{{value}}&lt;/p&gt;\n\n\n&quot;,&quot;script&quot;:&quot;\n    export default{\n        data(){\n            return {\n                value:false\n            }\n        }\n    }\n&quot;,&quot;style&quot;:null}">
                            <div><xl-checkbox v-model="value" label="选择框"></xl-checkbox>
<p>{{value}}</p>


</div>
                            <div slot="desc"><p>基础使用方式</p>
</div>
                            <div slot="source"><pre v-pre=""><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">xl-checkbox</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"value"</span> <span class="hljs-attr">label</span>=<span class="hljs-string">'选择框'</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">xl-checkbox</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">p</span>&gt;</span>{{value}}<span class="hljs-tag">&lt;/<span class="hljs-name">p</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
    <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span>{
        data(){
            <span class="hljs-keyword">return</span> {
                <span class="hljs-attr">value</span>:<span class="hljs-literal">false</span>
            }
        }
    }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
<h2 id="zi-ding-yi-zhi"><a class="header-anchor" href="#zi-ding-yi-zhi" aria-hidden="true">¶</a> 自定义值</h2>
<demo-block :jsfiddle="{&quot;html&quot;:&quot;&lt;xl-checkbox v-model=\&quot;value2\&quot; true-value=\&quot;TRUE\&quot; false-value=\&quot;FALSE\&quot; label=\&quot;选择框\&quot;&gt;&lt;/xl-checkbox&gt;\n&lt;p&gt;{{value2}}&lt;/p&gt;\n\n&quot;,&quot;script&quot;:&quot;\n    export default{\n        data(){\n            return {\n                value2:'TRUE'\n            }\n        }\n    }\n&quot;,&quot;style&quot;:null}">
