</div>
                            <div slot="source"><pre v-pre=""><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">xl-checkbox</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"value2"</span> <span class="hljs-attr">true-value</span>=<span class="hljs-string">'TRUE'</span> <span class="hljs-attr">false-value</span>=<span class="hljs-string">'FALSE'</span> <span class="hljs-attr">label</span>=<span class="hljs-string">'选择框'</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">xl-checkbox</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">p</span>&gt;</span>{{value2}}<span class="hljs-tag">&lt;/<span class="hljs-name">p</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
    <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span>{
        data(){
            <span class="hljs-keyword">return</span> {
                <span class="hljs-attr">value2</span>:<span class="hljs-string">'TRUE'</span>
            }
        }
    }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
<h2 id="jin-yong"><a class="header-anchor" href="#jin-yong" aria-hidden="true">¶</a> 禁用</h2>
<demo-block :jsfiddle="{&quot;html&quot;:&quot;&lt;xl-checkbox v-model=\&quot;value\&quot; disabled label=\&quot;选择框\&quot;&gt;&lt;/xl-checkbox&gt;\n&lt;p&gt;{{value}}&lt;/p&gt;\n\n&quot;,&quot;script&quot;:&quot;\n    export default{\n        data(){\n            return {\n                value:false\n            }\n        }\n    }\n&quot;,&quot;style&quot;:null}">
