<h2 id="bian-kuang"><a class="header-anchor" href="#bian-kuang" aria-hidden="true">¶</a> 边框</h2>
<demo-block :jsfiddle="{&quot;html&quot;:&quot;&lt;xl-checkbox v-model=\&quot;value\&quot; bordered label=\&quot;选择框\&quot;&gt;&lt;/xl-checkbox&gt;\n&lt;p&gt;{{value}}&lt;/p&gt;\n&quot;,&quot;script&quot;:null,&quot;style&quot;:null}">
