<h2 id="duo-xuan-kuang-zu"><a class="header-anchor" href="#duo-xuan-kuang-zu" aria-hidden="true">¶</a> 多选框组</h2>
<demo-block :jsfiddle="{&quot;html&quot;:&quot;&lt;xl-checkbox-group v-model=\&quot;arr\&quot;&gt;\n    &lt;xl-checkbox value=\&quot;a\&quot; label=\&quot;A\&quot;&gt;&lt;/xl-checkbox&gt;\n    &lt;xl-checkbox value=\&quot;b\&quot;&gt;&lt;/xl-checkbox&gt;\n    &lt;xl-checkbox value=\&quot;c\&quot; label=\&quot;C\&quot;&gt;&lt;/xl-checkbox&gt;\n&lt;/xl-checkbox-group&gt;\n&lt;p&gt;{{arr}}&lt;/p&gt;\n\n\n&quot;,&quot;script&quot;:&quot;\n    export default{\n        data(){\n            return {\n                arr:[]\n            }\n        }\n    }\n&quot;,&quot;style&quot;:null}">
                            <div><xl-checkbox-group v-model="arr">
    <xl-checkbox value="a" label="A"></xl-checkbox>
    <xl-checkbox value="b"></xl-checkbox>
    <xl-checkbox value="c" label="C"></xl-checkbox>
</xl-checkbox-group>
<p>{{arr}}</p>


</div>
                            <div slot="desc"><p>使用<code>xl-checkbox-group</code>实现多选框组,必须制定每个 checkbox 的<code>value</code>值，<code>label</code>不存在的时候，默认使用<code>value</code>值作为 label</p>
</div>
                            <div slot="source"><pre v-pre=""><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">xl-checkbox-group</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">'arr'</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">xl-checkbox</span> <span class="hljs-attr">value</span>=<span class="hljs-string">'a'</span> <span class="hljs-attr">label</span>=<span class="hljs-string">'A'</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">xl-checkbox</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">xl-checkbox</span> <span class="hljs-attr">value</span>=<span class="hljs-string">'b'</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">xl-checkbox</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">xl-checkbox</span> <span class="hljs-attr">value</span>=<span class="hljs-string">'c'</span> <span class="hljs-attr">label</span>=<span class="hljs-string">'C'</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">xl-checkbox</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">xl-checkbox-group</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">p</span>&gt;</span>{{arr}}<span class="hljs-tag">&lt;/<span class="hljs-name">p</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
    <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span>{
        data(){
            <span class="hljs-keyword">return</span> {
                <span class="hljs-attr">arr</span>:[]
            }
        }
    }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
<h2 id="duo-xuan-kuang-zu-ke-yi-he-grid-jie-he-shi-yong"><a class="header-anchor" href="#duo-xuan-kuang-zu-ke-yi-he-grid-jie-he-shi-yong" aria-hidden="true">¶</a> 多选框组可以和 grid 结合使用</h2>
<demo-block :jsfiddle="{&quot;html&quot;:&quot;&lt;xl-checkbox-group v-model=\&quot;arr2\&quot;&gt;\n    &lt;xl-row :gutter=\&quot;20\&quot;&gt;\n        &lt;xl-col :span=\&quot;8\&quot;&gt;\n            &lt;xl-checkbox value=\&quot;a\&quot;&gt;&lt;/xl-checkbox&gt;\n        &lt;/xl-col&gt;\n        &lt;xl-col :span=\&quot;8\&quot;&gt;\n            &lt;xl-checkbox value=\&quot;b\&quot;&gt;&lt;/xl-checkbox&gt;\n        &lt;/xl-col&gt;\n        &lt;xl-col :span=\&quot;8\&quot;&gt;\n            &lt;xl-checkbox value=\&quot;c\&quot; label=\&quot;C\&quot;&gt;&lt;/xl-checkbox&gt;\n        &lt;/xl-col&gt;\n    &lt;/xl-row&gt;\n&lt;/xl-checkbox-group&gt;\n&lt;p&gt;{{arr2}}&lt;/p&gt;\n\n\n&quot;,&quot;script&quot;:&quot;\n    export default{\n        data(){\n            return {\n                arr2:['123','456']\n            }\n        }\n    }\n&quot;,&quot;style&quot;:null}">
                            <div><xl-checkbox-group v-model="arr2">
    <xl-row :gutter="20">
        <xl-col :span="8">
            <xl-checkbox value="a"></xl-checkbox>
        </xl-col>
        <xl-col :span="8">
            <xl-checkbox value="b"></xl-checkbox>
        </xl-col>
        <xl-col :span="8">
            <xl-checkbox value="c" label="C"></xl-checkbox>
        </xl-col>
    </xl-row>
</xl-checkbox-group>
<p>{{arr2}}</p>


</div>
                            <div slot="desc"><p>结合使用<code>xl-checkbox-group</code>组件和<code>grid</code>组件实现布局</p>
</div>
                            <div slot="source"><pre v-pre=""><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">xl-checkbox-group</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">'arr2'</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">xl-row</span> <span class="hljs-attr">:gutter</span>=<span class="hljs-string">"20"</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">xl-col</span> <span class="hljs-attr">:span</span>=<span class="hljs-string">"8"</span>&gt;</span>
            <span class="hljs-tag">&lt;<span class="hljs-name">xl-checkbox</span> <span class="hljs-attr">value</span>=<span class="hljs-string">'a'</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">xl-checkbox</span>&gt;</span>
        <span class="hljs-tag">&lt;/<span class="hljs-name">xl-col</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">xl-col</span> <span class="hljs-attr">:span</span>=<span class="hljs-string">"8"</span>&gt;</span>
            <span class="hljs-tag">&lt;<span class="hljs-name">xl-checkbox</span> <span class="hljs-attr">value</span>=<span class="hljs-string">'b'</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">xl-checkbox</span>&gt;</span>
        <span class="hljs-tag">&lt;/<span class="hljs-name">xl-col</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">xl-col</span> <span class="hljs-attr">:span</span>=<span class="hljs-string">"8"</span>&gt;</span>
            <span class="hljs-tag">&lt;<span class="hljs-name">xl-checkbox</span> <span class="hljs-attr">value</span>=<span class="hljs-string">'c'</span> <span class="hljs-attr">label</span>=<span class="hljs-string">'C'</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">xl-checkbox</span>&gt;</span>
        <span class="hljs-tag">&lt;/<span class="hljs-name">xl-col</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">xl-row</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">xl-checkbox-group</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">p</span>&gt;</span>{{arr2}}<span class="hljs-tag">&lt;/<span class="hljs-name">p</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
    <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span>{
        data(){
            <span class="hljs-keyword">return</span> {
                <span class="hljs-attr">arr2</span>:[<span class="hljs-string">'123'</span>,<span class="hljs-string">'456'</span>]
            }
        }
    }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
<h2 id="an-niu-yang-shi-de-duo-xuan-kuang-zu"><a class="header-anchor" href="#an-niu-yang-shi-de-duo-xuan-kuang-zu" aria-hidden="true">¶</a> 按钮样式的多选框组</h2>
<demo-block :jsfiddle="{&quot;html&quot;:&quot;&lt;xl-checkbox-group type=\&quot;button\&quot;&gt;\n    &lt;xl-checkbox value=\&quot;123\&quot;&gt;aaa&lt;/xl-checkbox&gt;\n    &lt;xl-checkbox value=\&quot;456\&quot;&gt;bbb&lt;/xl-checkbox&gt;\n    &lt;xl-checkbox value=\&quot;987\&quot; label=\&quot;789\&quot;&gt;ccc&lt;/xl-checkbox&gt;\n&lt;/xl-checkbox-group&gt;\n&quot;,&quot;script&quot;:null,&quot;style&quot;:null}">
                            <div><xl-checkbox-group type="button">
    <xl-checkbox value="123">aaa</xl-checkbox>
    <xl-checkbox value="456">bbb</xl-checkbox>
    <xl-checkbox value="987" label="789">ccc</xl-checkbox>
</xl-checkbox-group>
</div>
                            <div slot="desc"><p>使用<code>type=&quot;button&quot;</code>实现多选框组</p>
</div>
                            <div slot="source"><pre v-pre=""><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">xl-checkbox-group</span> <span class="hljs-attr">type</span>=<span class="hljs-string">'button'</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">xl-checkbox</span> <span class="hljs-attr">value</span>=<span class="hljs-string">'123'</span>&gt;</span>aaa<span class="hljs-tag">&lt;/<span class="hljs-name">xl-checkbox</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">xl-checkbox</span> <span class="hljs-attr">value</span>=<span class="hljs-string">'456'</span>&gt;</span>bbb<span class="hljs-tag">&lt;/<span class="hljs-name">xl-checkbox</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">xl-checkbox</span> <span class="hljs-attr">value</span>=<span class="hljs-string">'987'</span> <span class="hljs-attr">label</span>=<span class="hljs-string">'789'</span>&gt;</span>ccc<span class="hljs-tag">&lt;/<span class="hljs-name">xl-checkbox</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">xl-checkbox-group</span>&gt;</span>
</code></pre>
</div></demo-block>
<h2 id="indeterminate-zhuang-tai"><a class="header-anchor" href="#indeterminate-zhuang-tai" aria-hidden="true">¶</a> indeterminate 状态</h2>
<demo-block :jsfiddle="{&quot;html&quot;:&quot;&lt;xl-checkbox :indeterminate=\&quot;indeterminate\&quot; :value=\&quot;checkAll\&quot; @click.prevent.native=\&quot;click\&quot;&gt;全选&lt;/xl-checkbox&gt;\n&lt;xl-checkbox-group type=\&quot;button\&quot; v-model=\&quot;arr3\&quot;&gt;\n    &lt;xl-checkbox value=\&quot;123\&quot;&gt;aaa&lt;/xl-checkbox&gt;\n    &lt;xl-checkbox value=\&quot;456\&quot;&gt;bbb&lt;/xl-checkbox&gt;\n    &lt;xl-checkbox value=\&quot;789\&quot;&gt;ccc&lt;/xl-checkbox&gt;\n&lt;/xl-checkbox-group&gt;\n&lt;p&gt;{{arr3}}&lt;/p&gt;\n\n&quot;,&quot;script&quot;:&quot;\n    export default{\n        data(){\n            return {\n                arr3:[]\n            }\n        },\n        computed:{\n            indeterminate(){\n                return this.arr3.length&gt;0 &amp;&amp; this.arr3.length&lt;3\n            },\n            checkAll() {\n                return this.arr3.length === 3\n            } \n        },\n        methods: {\n            click() {\n                if(!this.checkAll){\n                    this.arr3 = ['123','456','789']\n                }else{\n                    this.arr3 = []\n                }\n            }\n        }\n    }\n&quot;,&quot;style&quot;:null}">
                            <div><xl-checkbox :indeterminate="indeterminate" :value="checkAll" @click.prevent.native="click">全选</xl-checkbox>
<xl-checkbox-group type="button" v-model="arr3">
    <xl-checkbox value="123">aaa</xl-checkbox>
    <xl-checkbox value="456">bbb</xl-checkbox>
    <xl-checkbox value="789">ccc</xl-checkbox>
</xl-checkbox-group>
<p>{{arr3}}</p>

</div>
                            <div slot="desc"><p>使用<code>indeterminate</code>实现多选框组</p>
</div>
                            <div slot="source"><pre v-pre=""><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">xl-checkbox</span> <span class="hljs-attr">:indeterminate</span>=<span class="hljs-string">"indeterminate"</span> <span class="hljs-attr">:value</span>=<span class="hljs-string">"checkAll"</span> <span class="hljs-attr">@click.prevent.native</span>=<span class="hljs-string">"click"</span>&gt;</span>全选<span class="hljs-tag">&lt;/<span class="hljs-name">xl-checkbox</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">xl-checkbox-group</span> <span class="hljs-attr">type</span>=<span class="hljs-string">'button'</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">'arr3'</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">xl-checkbox</span> <span class="hljs-attr">value</span>=<span class="hljs-string">'123'</span>&gt;</span>aaa<span class="hljs-tag">&lt;/<span class="hljs-name">xl-checkbox</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">xl-checkbox</span> <span class="hljs-attr">value</span>=<span class="hljs-string">'456'</span>&gt;</span>bbb<span class="hljs-tag">&lt;/<span class="hljs-name">xl-checkbox</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">xl-checkbox</span> <span class="hljs-attr">value</span>=<span class="hljs-string">'789'</span>&gt;</span>ccc<span class="hljs-tag">&lt;/<span class="hljs-name">xl-checkbox</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">xl-checkbox-group</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">p</span>&gt;</span>{{arr3}}<span class="hljs-tag">&lt;/<span class="hljs-name">p</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
    <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span>{
        data(){
            <span class="hljs-keyword">return</span> {
                <span class="hljs-attr">arr3</span>:[]
            }
        },
        <span class="hljs-attr">computed</span>:{
            indeterminate(){
                <span class="hljs-keyword">return</span> <span class="hljs-keyword">this</span>.arr3.length&gt;<span class="hljs-number">0</span> &amp;&amp; <span class="hljs-keyword">this</span>.arr3.length&lt;<span class="hljs-number">3</span>
            },
            checkAll() {
                <span class="hljs-keyword">return</span> <span class="hljs-keyword">this</span>.arr3.length === <span class="hljs-number">3</span>
            } 
        },
        <span class="hljs-attr">methods</span>: {
            click() {
                <span class="hljs-keyword">if</span>(!<span class="hljs-keyword">this</span>.checkAll){
                    <span class="hljs-keyword">this</span>.arr3 = [<span class="hljs-string">'123'</span>,<span class="hljs-string">'456'</span>,<span class="hljs-string">'789'</span>]
                }<span class="hljs-keyword">else</span>{
                    <span class="hljs-keyword">this</span>.arr3 = []
                }
            }
        }
    }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
<h2 id="checkbox-shu-xing"><a class="header-anchor" href="#checkbox-shu-xing" aria-hidden="true">¶</a> checkbox 属性</h2>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>label</td>
<td>checkbox 显示值</td>
<td>string</td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>name</td>
<td>checkbox 的 name（单独使用时有效）</td>
<td>string</td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>value</td>
<td>checkbox 单独使用时，支持 v-model 绑定</td>
<td>string / number / boolean</td>
<td>-</td>
<td>false</td>
</tr>
<tr>
<td>true-value</td>
<td>选中时的 value 值（单独使用时有效）</td>
<td>string / number / boolean</td>
<td>-</td>
<td>true</td>
</tr>
<tr>
<td>false-value</td>
<td>未选中时的 value 值（单独使用时有效）</td>
<td>string / number / boolean</td>
<td>-</td>
<td>false</td>
</tr>
<tr>
<td>bordered</td>
<td>是否显示 checkbox 边框</td>
<td>boolean</td>
<td>-</td>
<td>false</td>
</tr>
<tr>
<td>readonly</td>
<td>是否只读</td>
<td>boolean</td>
<td>-</td>
<td>false</td>
</tr>
<tr>
<td>disabled</td>
<td>是否禁用状态</td>
<td>boolean</td>
<td>-</td>
<td>false</td>
</tr>
<tr>
<td>indeterminate</td>
<td>半选状态 (一旦为 true，多选框就不能为其他状态)</td>
<td>boolean</td>
<td>-</td>
<td>false</td>
</tr>
</tbody>
</table>
<h2 id="checkbox-shi-jian"><a class="header-anchor" href="#checkbox-shi-jian" aria-hidden="true">¶</a> checkbox 事件</h2>
<table class="table"><thead>
<tr>
<th>事件名</th>
<th>说明</th>
<th>参数</th>
</tr>
</thead>
<tbody>
<tr>
<td>on-change</td>
<td>checkbox 改变事件</td>
<td>选中时的值（trueValue）</td>
</tr>
<tr>
<td>on-focus</td>
<td>checkbox 获得焦点事件</td>
<td>表单元素事件 e</td>
</tr>
<tr>
<td>on-blur</td>
<td>checkbox 失去焦点事件</td>
<td>表单元素事件 e</td>
</tr>
</tbody>
</table>
<h2 id="checkbox-slots"><a class="header-anchor" href="#checkbox-slots" aria-hidden="true">¶</a> checkbox slots</h2>
<table class="table"><thead>
<tr>
<th>名称</th>
<th>说明</th>
</tr>
</thead>
<tbody>
<tr>
<td>default</td>
<td>checkbox 显示的 label，如果没有设置插槽，默认使用 label 参数，如果 label 也没有，就为空</td>
</tr>
</tbody>
</table>
<h2 id="checkbox-group-shu-xing"><a class="header-anchor" href="#checkbox-group-shu-xing" aria-hidden="true">¶</a> checkbox group 属性</h2>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>value</td>
<td>选中的 checkbox v-model 绑定</td>
<td>array</td>
<td>-</td>
<td>[]</td>
</tr>
<tr>
<td>name</td>
<td>多选框选框组的名称 ，默认随机生成</td>
<td>string</td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>type</td>
<td>多选框组类型</td>
<td>string</td>
<td>button</td>
<td>-</td>
</tr>
</tbody>
</table>
<h2 id="checkbox-group-shi-jian"><a class="header-anchor" href="#checkbox-group-shi-jian" aria-hidden="true">¶</a> checkbox group 事件</h2>
<table class="table"><thead>
<tr>
<th>事件名</th>
<th>说明</th>
<th>参数</th>
</tr>
</thead>
<tbody>
<tr>
<td>on-change</td>
<td>checkbox 选中事件</td>
<td>选中时的 checkbox 的值</td>
</tr>
</tbody>
</table>
<h2 id="checkbox-group-slots"><a class="header-anchor" href="#checkbox-group-slots" aria-hidden="true">¶</a> checkbox group slots</h2>
<table class="table"><thead>
<tr>
<th>名称</th>
<th>说明</th>
</tr>
</thead>
<tbody>
<tr>
<td>default</td>
<td>用来设置 checkbox 组</td>
</tr>
</tbody>
</table>
</section></template>

<script>
    export default{
        data(){
            return {
                value:false,
                value2:'TRUE',
                arr:[],
                arr2:[],
                arr3:[],
            }
        },
        computed:{
            indeterminate(){
                return this.arr3.length>0 && this.arr3.length<3
            },
            checkAll() {
                return this.arr3.length === 3
            } 
        },
        methods: {
            click() {
                if(!this.checkAll){
                    this.arr3 = ['123','456','789']
                }else{
                    this.arr3 = []
                }
            }
        }
    }
</script>


